.contactme .form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 50%;
  overflow: hidden;
}
.contactme .form input,
.contactme .form textarea {
  border: none;
  color: rgb(255, 255, 255);
  width: 80%;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 3px;
  margin: 20px;
  background: transparent;
  border-bottom: white solid 1px;
  resize: vertical;
  outline: none;
}
.contactme .form .submit-btn {
  color: white;
  width: 200px;
  border: solid rgba(255, 255, 255, 0.616) 1px;
  border-radius: 30px;
  cursor: pointer;
  transition: 0.3s;
}
.contactme .form .submit-btn:hover {
  border: solid rgba(255, 255, 255) 1px;
}

.contact-header {
  text-align: center;
  font-size: 45px;
  letter-spacing: 3px;
  margin-top: 100px;
}
.contactme {
  margin: 100px auto;
  width: 60%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  overflow: hidden;
}
.contactme .myinfo {
  padding: 20px;
  flex: 50%;
  display: flex;
  flex-direction: column;
  
}
.contactme .myinfo p {
  padding: 10px 10px 0px 10px;
  opacity: 0.5;
}
.contactme .myinfo h1 {
  font-weight: 100;
  padding: 3px 0px 10px 10px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
@media only screen and (max-width: 920px) {
  .contactme {
    margin: 10px;
    width: 100%;
  }
  .contactme .form input,
  .contactme .form textarea {
    width: 90%;
    font-size: 15px;
    letter-spacing: 2px;
    margin: 16px;
  }
  .contact-header {
    font-size: 40px;
  }
  .contactme .myinfo {
    padding: 10px;
  }
  .contactme .myinfo p {
    padding: 5px;
   
  }
  .contactme .myinfo h1 {
    font-size: 22px;
    font-weight: 100;
    padding: 5px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
}
