.testimonial-header {
  color: white;
  text-align: center;
  margin: 100px 0px 10px 0px;
  font-size: 40px;
  letter-spacing: 3px;
  overflow-x: hidden;
}
.slide-container .each-slide {
  margin: 20px 100px;
  
}
.slide-container .each-slide .text {
  padding: 50px;
  letter-spacing: 3px;
  font-size: 18px;
  line-height: 30px;
  background-color: #212121;
  position: relative;
  
  
}
.slide-container .each-slide .text::before {
  content: "";
  position: absolute;
  left: 10%;
  bottom: -10px;
  height: 10px;
  width: 80%;
  background-color: red;
  overflow-x: hidden;
}
.slide-container .each-slide .image img {
  height: 100px;
  border-radius: 100%;
  z-index: 9;
  position: relative;
  top: -44px;
  overflow-x: hidden;
}

.slide-container .each-slide .image {
  text-align: center;
  
}
@media only screen and (max-width: 920px) {
  .slide-container .each-slide {
    margin: 20px 10px;
  }
  .slide-container .each-slide .text {
    padding: 10px 10px 50px 10px;
    letter-spacing: 2px;
    font-size: 15px;
    line-height: 26px;
  }
  .testimonial-header {
    font-size: 30px;
  }
}
