nav .menu{
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
nav .menu li a{
    display: inline-block;
    padding: 10px 20px;
    font-size: 20px;
    margin: 0px 20px;
    text-decoration: none;
    color: white;
}
nav .menu li a:hover{
    transition: 0.5s;
    color: rgb(255, 78, 78);
}
nav{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px;
    background-color: rgba(0, 0, 0, 0.829);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 21;
    box-shadow: 0px 0px 10px rgb(119, 119, 119) ;
}
nav ul .header h1{
    font-size: 35px;
    letter-spacing: 3px;
    color: aliceblue;
} 
nav ul .header h1 .firstletter{
    display: inline-block;
    transform: rotate(-20deg);
    color: rgb(255, 78, 78);
}
.sidemenu{
    display: none;
}

@media only screen and (max-width: 920px){
    nav ul .header h1{
        font-size: 30px;
        letter-spacing: 3px;
        color: aliceblue;
    } 
    .hamburger{
        
        height: 50px;
        width: 40px;
        position: absolute;
        top: 10px;
        right: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        overflow: hidden;
        
    }
    
    .hamburger div{
        border: solid 1px rgb(255, 78, 78);
        color: rgb(255, 255, 255);
        transition: 0.6s;
    }
    nav ul li{
        display: none;
    }
    .sidemenu{
        color: aqua;
        position: fixed;
        display: flex;
        flex-direction: column;
        list-style: none;
        background-color: rgba(0, 0, 0, 0.829);
        text-align: center;
        overflow: hidden;
        z-index: 65465;
        top:70px;
        height:43%;
        
    }
    .sidemenu li{
        transition: 0.6s;
        padding: 10px;
    }
    .sidemenu li a{
        display: inline-block;
        transition: 0.6s;
        padding: 10px 20px;
        text-decoration: none;
        color: white;
        letter-spacing: 2px;
        transition: 0.6s;
    }
    .sidemenu li a:hover{
        letter-spacing: 5px;
        color: rgb(255, 78, 78) ;
        
    }
    
    
}