.Portfolio-page {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
  justify-content: center;
  background-color: black;
  color: white;
  width: 100%;
  overflow-x: hidden;
}
.Portfolio-page h2 {
  text-align: center;
  letter-spacing: 2px;
  font-family: serif;
}
.Portfolio-page .heading {
  position: absolute;
  top: 94%;
  font-size: 40px;
  letter-spacing: 4px;
  font-family: "Courier New", Courier, monospace;
}

/* Create four equal columns that sits next to each other */
.column {
  flex: 25%;
  max-width: 25%;
  padding: 0 4px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
  cursor: pointer;
  transition: 0.6s;
  position: relative;
  overflow-x: hidden;
}

.column img:hover {
  filter: blur(2px);
  opacity: 0.4;
  overflow-x: hidden;
}


/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 800px) {
  .column {
    flex: 50%;
    max-width: 50%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
    flex: 100%;
    max-width: 100%;
  }
  
  .Portfolio-page h2 {
    font-size: 20px;
  }
}
