.About{
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    overflow: hidden;
}
.about-header{
    letter-spacing: 5px;
    font-size: 45px;
    text-align: center;
    margin: 100px 0px 20px 0px;
    overflow: hidden;
}
.About .text{
    font-size: 20px;
    letter-spacing: 3px;
    line-height: 30px;margin:100px 0px 50px 0px;
    width: 700px;
    overflow: hidden;
}
.About .text button{
    color: rgb(255, 78, 78);
    background: transparent;
    border:rgb(255, 78, 78) solid 1px ;
    padding: 10px 30px;
    margin:15px 0px;
    font-size: 18px;
    border-radius: 20px;
    cursor: pointer;
    outline: none;
    letter-spacing: 5px;
    font-weight: bold;
    transition: 0.6s;
    
}
.About .text button:hover{
    box-shadow: 0px 0px 10px rgb(255, 78, 78) ;
}
@media only screen and (max-width: 920px){
    .About .text{
        width: 100vw;
        padding:0px 15px;
    }
    
}