.container {
  margin-top: 90px;
}
.service_images {
  display: flex;
  color: white;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  overflow-x: hidden;
}
.service_images img {
  height: 200px;
  
}
.service_images .image {
  width: 500px;
  text-align: center;
  padding: 10px;
  margin: 20px;
  box-shadow: 0px 0px 10px rgb(100, 100, 100);
  border-radius: 10px;
  overflow: hidden;
}
.service_images .image p {
  padding: 10px;
  letter-spacing: 2px;
  line-height: 25px;
  overflow-x: hidden;
}
.service_heading {
  text-align: center;
  color: white;
  letter-spacing: 4px;
  font-size: 50px;
  margin: 20px;
  font-family: "Courier New", Courier, monospace;
  overflow-x: hidden;
}
