
.fullpage {
  height: 100vh;
  width: 100%;
  background: black url("/images/dhruv.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  overflow: hidden;
  color: white;
  text-align: center;
  font-family: "serif";
  
}

.title h1 {
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4em;
  letter-spacing: 7px;
  height: 120px;

  width: 700px;
}
.title h1::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 78, 78);
  animation: label 2s ease-in-out 0.6s 1 forwards;
  width: 0;
}
.skills-brief {
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 25px;
  overflow: hidden;
}
.skills-brief h2 {
  padding: 10px;
}
.skills-brief p {
  padding: 10px;
  
}
.skills-brief p:nth-child(5) {
  font-weight: bolder;
}
@keyframes label {
  0% {
    width: 0;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}

.scroll-downs {
  margin-top: 755px;
  margin-left: 50%;
  transform: translate(-50%,0);
  width :34px;
  height: 55px;
}
.mousey {
  
  width: 3px;
  padding: 10px 15px;
  height: 35px;
  border: 2px solid rgb(255, 78, 78);
  border-radius: 25px;
  opacity: 0.75;
  box-sizing: content-box;
}
.scroller {
  width: 3px;
  height: 10px;
  border-radius: 25%;
  background-color: rgb(255, 78, 78);;
  animation-name: scroll;
  animation-duration: 2.2s;
  animation-timing-function: cubic-bezier(.15,.41,.69,.94);
  animation-iteration-count: infinite;
}
@keyframes scroll {
  0% { opacity: 0; }
  10% { transform: translateY(0); opacity: 1; }
  100% { transform: translateY(15px); opacity: 0;}
}

@media only screen and (max-width: 920px) {
  .dhruv {
    height: 400px;
    width: 300px;
  }
  .title h1 {
    top: 25%;
    font-size: 2em;
    letter-spacing: 3px;
    height: 100px;
    width: 100%;
  }
  .skills-brief {
    position: absolute;
    top: 57%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    width: 100%;
  }
  .scroll-downs {
    margin: auto;
    margin-top: 70vh;
    transform: translate(0,0);
    width :34px;
    height: 55px;
  }
}
