* {
  color: white;
  
}
.Counters{
  display: flex;
  justify-content:space-evenly ;
  align-items: center;
  flex-wrap: wrap;
  
}
.Counters .count{
  color:rgb(255, 78, 78);
  font-size: 90px;
}
.Counters div{
  width: 200px;
  text-align: center;
}
.Counters p{
  letter-spacing: 2px;
  font-size: 18px;
}
.skill-heading{
  letter-spacing:5px ;
  margin:100px 0px 50px 0px;
  font-size: 24px;
  text-align: center;
  
}
@media only screen and (max-width: 920px){
  .Counters div{
    width: 150px;
    margin: 20px 0px;
  }
  .Counters .count{
    font-size: 60px;
  }
  .Counters p{
    font-size: 15px;
  }
  .skill-heading{
    
    
    font-size: 20px;
    text-align: center;
    
  }
}