
.footer h1 {
  text-align: center;
  font-size: 40px;
  letter-spacing: 3px;
  margin-top: 100px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
.footer .icons img {
  cursor: pointer;
  transition: 0.6s;
  padding: 20px;
  width: 100px;
}
.footer .icons {
  display: flex;
  justify-content: space-evenly;
  margin-top: 80px;
  flex-wrap: wrap;
}
.footer .icons img:hover {
  transform: rotate(360deg) scale(1.3);
}
.footer p {
  margin: 50px;
  text-align: center;
  opacity: 0.6;
}
@media only screen and (max-width: 920px) {
  .footer .icons img {
    width: 80px;
  }
  .footer h1 {
    font-size: 35px;
  }
  .footer .icons {
    margin-top: 40px;
  }
}
